	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import forum from '@/views/forum/list'
	import qiye from '@/views/qiye/list'
	import news from '@/views/news/list'
	import gangweileixing from '@/views/gangweileixing/list'
	import zhaopinxinxi from '@/views/zhaopinxinxi/list'
	import toudijianli from '@/views/toudijianli/list'
	import yonghu from '@/views/yonghu/list'
	import mianshijieguo from '@/views/mianshijieguo/list'
	import chayuejianli from '@/views/chayuejianli/list'
	import storeup from '@/views/storeup/list'
	import config from '@/views/config/list'
	import mianshitongzhi from '@/views/mianshitongzhi/list'
	import qiyeRegister from '@/views/qiye/register'
	import qiyeCenter from '@/views/qiye/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/qiyeCenter',
			name: '企业个人中心',
			component: qiyeCenter
		}
		,{
			path: '/forum',
			name: '我的发布',
			component: forum
		}
		,{
			path: '/qiye',
			name: '企业',
			component: qiye
		}
		,{
			path: '/news',
			name: '公告信息',
			component: news
		}
		,{
			path: '/gangweileixing',
			name: '岗位类型',
			component: gangweileixing
		}
		,{
			path: '/zhaopinxinxi',
			name: '招聘信息',
			component: zhaopinxinxi
		}
		,{
			path: '/toudijianli',
			name: '投递简历',
			component: toudijianli
		}
		,{
			path: '/yonghu',
			name: '用户',
			component: yonghu
		}
		,{
			path: '/mianshijieguo',
			name: '面试结果',
			component: mianshijieguo
		}
		,{
			path: '/chayuejianli',
			name: '查阅简历',
			component: chayuejianli
		}
		,{
			path: '/storeup',
			name: '我的收藏',
			component: storeup
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		,{
			path: '/mianshitongzhi',
			name: '面试通知',
			component: mianshitongzhi
		}
		]
	},
	{
		path: '/qiyeRegister',
		name: '企业注册',
		component: qiyeRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
